<template>
  <div class="contactPage">
    <!-- Head Title -->
    <van-row class="headTitle">
      <div @click="goBack">
        <img :src="arrowLeft" style="width:0.16rem;height:0.26rem;" />
        {{$t('message.transferPayment.contact')}}
      </div>
    </van-row>
    <!-- html content -->
       <ul style="width:100%">
          <li v-for="(item,index) in contactList" :key="index" @click="choose(item)"  :class="getClass()">
           <img :src="contactIcon" style="width:0.64rem;height:0.64rem;" />
          <div>
            <span style="text-align:left;font-size:0.26rem;color:#555b61;">{{item.name}}</span>
            <span style="font-size:0.24rem;color:#555b61;color:#9ba0a5;">{{getAddress(item.address)}}</span>
          </div>
          </li>
    </ul>
  </div>
</template>
<script>
import arrowLeft from "@/images/language_left.png";
import contactIcon from "@/images/contact.png";
import Lockr from "lockr";
export default {
  data() {
    return {
      arrowLeft,
      contactIcon,
      contactList: [],
      clickBg: false,
      currentShow: ""
    };
  },
  created() {
    this.currentShow = this.$route.query.currentShow;
  },
  mounted() {
    console.log(Lockr.get("saveContact"));
    if (Lockr.get("saveContact")) {
      this.contactList = Lockr.get("saveContact");
    }
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "TransferPayment",
        query: {
          currentShow: this.currentShow
        }
      });
    },
    getClass() {
      if (this.clickBg) {
        return "clickStyle";
      }
      return "contactStyle";
    },
    choose(item) {
      this.clickBg = true;
      this.$router.push({
        name: "TransferPayment",
        query: {
          currentShow: this.currentShow,
          address: item.address,
          name: item.name
        }
      });
    },
    getAddress(addr) {
      if (addr) {
        let address = addr.substr(0, 6) + "****" + addr.substr(-4);
        return address;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.contactPage {
  background-color: #fafafa;
  font-family: "PingFangSc-Regular";
  .headTitle {
    width: 100%;
    height: 0.88rem;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.28rem;
    color: #fff;
    background-color: #005c9c;
    img {
      width: 0.24rem;
      height: 0.36rem;
      position: absolute;
      left: 0.2rem;
      top: 0.28rem;
    }
  }
  .clickStyle {
    display: flex;
    padding: 0.2rem 0 0.18rem 0.2rem;
    height: 0.66rem;
    background-color: #edf5ff;
    // line-height: 0.8rem;
    border-bottom: 0.01rem solid #ebebed;
    div {
      height: 100%;
      margin-left: 0.2rem;
      display: flex;
      flex-direction: column;
    }
    span {
      height: 50%;
    }
  }
  .contactStyle {
    display: flex;
    padding: 0.2rem 0 0.18rem 0.2rem;
    height: 0.66rem;
    // line-height: 0.8rem;
    border-bottom: 0.01rem solid #ebebed;
    div {
      height: 100%;
      margin-left: 0.2rem;
      display: flex;
      flex-direction: column;
    }
    span {
      height: 50%;
    }
  }
}
</style>



